@import '~antd/lib/style/themes/default.less';
body {
  margin: 0;
  font-family: 'Libre Franklin', sans-serif;
  font-size: 16px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.connect-wallet {
  display: flex;
  justify-content: center;
  align-items: center;
}

.wallet-login {
  min-width: 7.25rem;
  height: 2.88rem;
  background: #483fd7;
  border-radius: 0.31rem;
  margin-right: 0.75rem;
  font-size: 0.88rem;
  line-height: 2.88rem;
  padding: 0 0.63rem;
  span {
    margin-right: 0 !important;
  }
  @media screen and (max-width: @screen-md) {
    margin-right: 0;
    height: 3.38rem;
    font-size: 1rem;
  }
}
.wallet-btn-active {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem !important;
  min-width: 4.25rem;
}
.wallet-btn-active-img {
  height: 2.88rem;
  width: 2.88rem;
  border-radius: 100%;
}

.no-chainId-tips {
  background-color: #4293fe;
}

.gray {
  color: #8c8c8c !important;
}

.language-move {
  box-shadow: @shadow-2;
}

.choose-info {
  border-radius: @border-radius-base;
  .choose-icon {
    font-size: 1rem;
    color: #483fd7 !important;
    position: absolute;
    bottom: calc(1.88rem + 1.25rem);
    right: calc(50% - 1.88rem);
    width: 1.25rem;
    height: 1.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    @media screen and (max-width: @screen-md) {
      width: 1.88rem;
      height: 1.88rem;
      font-size: 1.5rem;
      right: calc(50% - 2.88rem);
    }
  }
}

.loading-wrapper {
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.2);
  position: fixed;
}

.bjLogo {
  width: 18px;
  height: 18px;
  margin-left: 10px;
}

.message-global {
  // top: 0;
  // margin-bottom: 0.5rem;
  // width: 100%;
  // min-height: 3.75rem;
  // background: rgba(52, 70, 234, 0.7);
  // box-shadow: 0 0.19rem 0.38rem -0.25rem rgb(0 0 0 / 12%), 0 0.38rem 1rem 0 rgb(0 0 0 / 8%), 0 0.56rem 1.75rem 0.5rem rgb(0 0 0 / 5%);
  // .ant-message-notice-content {
  //   box-shadow: none;
  //   background: transparent;
  //   font-weight: 400;
  //   color: #fff;
  // }
}
.wallets {
  h2 {
    font-size: 1.63rem;
    font-family: Source Han Sans SC;
    font-weight: bold;
    color: #293543;
  }
  .contents {
    padding: 1.25rem 3.25rem 2.13rem;
  }
  @media screen and (max-width: @screen-md) {
    .contents {
      padding: 0 2.25rem 0;
    }
  }
}
.wallets .ant-modal-close {
  top: 1.38rem;
  right: 1.24rem;
  :hover {
    color: #293543;
  }
  @media screen and (max-width: @screen-md) {
    top: 0.88rem;
    right: 1.24rem;
  }
}

.navlink-titles {
  font-size: 1rem;
  font-weight: bold;
  color: #293543;
  line-height: 3rem !important;
  min-height: 3rem !important;
  height: auto !important;
  padding: 0 1.63rem;
}

.active-two {
  background: #f2f2ff !important;
}

.iconss {
  font-size: 3rem;
  color: #a2a2a2;
  cursor: pointer;
  margin-left: 12px;
}

.h5-drawer {
  .ant-drawer-body {
    padding: 0 0.63rem 0 0.63rem;
  }
  .tabbar-right {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
  }
}

.no-network-btns {
  font-size: 1rem;
  font-weight: 500;
  margin-left: 1.25rem;
  color: #483fd7;
  border-color: #fff;
  border-radius: 1.81rem;
  &:hover,
  &:focus {
    border-color: #fff;
    color: #483fd7;
    background: #fff;
  }
}

.home-tag-btn {
  min-width: 11.25rem;
  border-radius: 0.31rem;
  span {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  @media screen and (max-width: @screen-md) {
    min-width: 10.25rem;
  }
  @media screen and (max-width: @screen-xl) {
    max-width: calc(25vw - 1rem);
    min-width: auto;
  }
}

.home-tag-btn1 {
  min-width: 7.5rem;
  border-radius: 0.31rem;
  span {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  @media screen and (max-width: @screen-md) {
    min-width: 10.25rem;
    margin-bottom: 1.25rem;
  }
}

.image-cur {
  background: rgba(72, 63, 215, 0.1);
  width: 37.5rem;
  height: 28.13rem;
}

.noactive {
  background: #483fd7;
}

.drawer-mask {
  .ant-drawer-content {
    border-radius: 1.5rem 1.5rem 0 0;
  }
  .ant-drawer-body {
    display: flex;
    flex-direction: column;
    padding: 0;
  }
  .drawer-content {
    display: flex;
    flex-direction: column;
  }
  .drawer-info {
    margin-top: 2.5rem;
  }
  .drawer-span {
    width: 15rem;
    height: 4.25rem;
    background: #ffffff;
    border: 1px solid #2f303b;
    border-radius: 2.13rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 1.5rem;
    font-weight: 600;
    color: #2f303b;
    &:hover {
      border: 1px solid #3446ea;
      color: #3446ea;
    }
  }
  .ant-drawer-close {
    position: absolute;
    right: 0;
  }
  .active-info {
    color: #483fd7;
    h3 {
      color: #483fd7;
    }
  }
}

.drawer-mask-wallet {
  .ant-drawer-body {
    padding: 24px;
  }
}
